body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Inter', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{width:100%;height:100vh;background-color:#f9fafb;overflow-x:hidden}.App .flex-components{display:flex}

.navbar{position:absolute;width:100%;background-color:#ffffff;z-index:2;top:0;left:0;box-shadow:0 1px 2px 0 rgba(0,0,0,0.06)}.navbar p{margin-left:18px;font-size:13px;font-weight:normal;font-stretch:normal;font-style:normal;line-height:1.62;letter-spacing:normal;color:#242b39}

.sidebar{display:flex;flex-direction:column;height:100%;width:16%;position:fixed;z-index:1;top:0;left:0;overflow-x:hidden;box-shadow:0 2px 8px 0 rgba(0,0,0,0.15);background-color:#ffffff;padding:100px 2% 0 1%;letter-spacing:0.6px;color:#9fa7bb}@media (max-width: 1200px){.sidebar{padding:100px 0 0 0px}}.sidebar p{margin-left:5%;font-size:10px}.sidebar a{width:100%;display:flex;align-items:center;padding:11px 7px 12px 10px;border-radius:8px;margin-bottom:10px;font-size:15px;font-weight:500;line-height:1.47;letter-spacing:normal;color:#242b39;text-decoration:none}@media (max-width: 1200px){.sidebar a{width:100%;font-size:14px}}.sidebar a img{margin-left:1%;margin-right:12px}@media (max-width: 1200px){.sidebar a img{display:none}}.sidebar .active{border-radius:8px;background-color:#f1f4f8}@media (max-width: 1200px){.sidebar .active{border-radius:0px}}

.controllers{width:100%;display:flex;justify-content:space-between;align-items:center;margin-bottom:30px}.controllers .SearchBar{display:flex;justify-content:center;align-items:center;border:solid 1px #e2e7ee;border-radius:3px;background-color:#ffffff;padding:5px 8px}.controllers .SearchBar img{width:16px;height:16px}.controllers .SearchBar input{padding-left:8px;border:none;line-height:1.62;width:251px;font-size:13px;color:#9fa7bb}.controllers .SearchBar input:focus{outline:none}.controllers .plus{border:0px;width:30px;height:30px;border-radius:15px;background-color:#407cff;box-shadow:0 1px 2px 0 rgba(0,0,0,0.2);display:flex;justify-content:center;align-items:center}.controllers .plus:focus{outline:none}.controllers .plus:hover{cursor:pointer}

.customers{width:100%;padding:100px 4% 0 22%}.customers h1{font-size:31px;font-weight:500;font-stretch:normal;font-style:normal;line-height:normal;letter-spacing:-0.37px;color:#000000}.customers .all{width:100%;background-color:#ffffff;border-radius:4px;box-shadow:0 1px 2px 0 rgba(0,0,0,0.05);border:solid 1px #e2e7ee}.customers .all .head{font-size:10px;font-weight:bold;background:#e2e7ee;display:flex;border-radius:4px;border-bottom:solid 1px #e2e7ee;background-color:#f1f4f8;padding:9px  20px;letter-spacing:0.6px;color:#516173;margin-bottom:10px}.customers .all .head p{margin:0px}.customers .all .head .right{width:45%;display:flex;margin-left:auto;justify-content:space-between;align-items:center}.customers .all .head .right p{text-align:center;width:55px}.customers .all .head .right .size{text-align:right}.customers .all .head .right .size:hover{cursor:pointer}.customers .all .head .right .size img{margin:0 0 -1px 3px}.customers .all .customer{display:flex;align-items:center;font-size:13px;color:#242b39;padding:3px  20px}.customers .all .customer:hover{background-color:#eff2f7}.customers .all .customer .companyName{width:28%;height:35px;display:flex;align-items:center}.customers .all .customer .companyName .logo{display:flex;align-items:center;justify-content:center;width:30px;height:30px;margin-right:5px}.customers .all .customer .companyName .logo .img-inline{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;max-width:24px;display:inline;margin:0;padding:0;vertical-align:baseline}.customers .all .customer .dropdown{position:relative}.customers .all .customer .dropdown button{width:77px;height:25px;font-weight:bold;padding:5px 8px 4px;border-radius:4px;box-shadow:0 1px 2px 0 rgba(0,0,0,0.05);border:solid 1px #e2e7ee;background-color:#ffffff;color:#2f3748;cursor:pointer;position:relative}.customers .all .customer .dropdown button:focus{outline:none}.customers .all .customer .dropdown ul{width:234px;display:block;position:absolute;background-color:#f9f9f9;box-shadow:0px 8px 16px 0px rgba(0,0,0,0.2);z-index:1;list-style-type:none;padding:0}.customers .all .customer .dropdown ul li{vertical-align:baseline;text-decoration:none;padding:10px 3px 10px 14px;font-size:13px;font-weight:500;font-stretch:normal;font-style:normal;line-height:1.62;letter-spacing:normal;color:#2f3748}.customers .all .customer .dropdown ul li:hover{background-color:#f1f4f8;cursor:pointer}.customers .all .customer .right{display:flex;align-items:center;font-size:13px;color:#242b39;width:45%;margin-left:auto;justify-content:space-between}.customers .all .customer .right p{margin:0;text-align:center;width:55px}.customers .all .customer .right .status{text-transform:uppercase;border-radius:4px;background-color:#cffbe9;font-size:10px;font-weight:500;line-height:1.6;letter-spacing:1px;color:#21985e}

